*{
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
}

body{
    background-color: rgb(240, 240, 240);
}
.header {
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(31, 31, 56);
    width: 100%;
}

.logo {
    font-size: 1rem;
    padding-left: 10px;
    color: white;
}

.account {
    list-style: none;
    text-decoration: none;
    color: white;
    padding-right: 10px;
}

.navbar {
    height: 75px;
    background-color: rgb(236, 236, 236);
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-bottom: 2px solid lightgray;
    position: sticky;
    top:0%;
    z-index: 1;
}

.nav-links {
    display: flex;
    list-style: none;
}
.nav-links li {
    height: 75px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 20px;
    transition: all 0.8s ease;
}
.home,
.services,
.faqs,
.contact {
    text-decoration: none;
    font-size: 1rem;
    color: rgb(31, 31, 56);

}


.home:hover li{
    background-color: rgb(31, 31, 56);
    color: white;
    border: 1px solid gray;
    border-radius: 5px;
}

.services:hover li{
    background-color: rgb(31, 31, 56);
    color: white;
    border: 1px solid gray;
    border-radius: 5px;
}

.faqs:hover li{
    background-color: rgb(31, 31, 56);
    color: white;
    border: 1px solid gray;
    border-radius: 5px;
}

.contact:hover li{
    background-color: rgb(31, 31, 56);
    color: white;
    border: 1px solid gray;
    border-radius: 5px;
}

.nav-menu {
    display: none;
}



.nav-pic {
    display: none;
}

@media screen and (max-width:780px){
    .nav-link-mobile {
        position: absolute;
        display: block;
        left: 0%;
        top: 40px;
        transition: all 0.5s ease;
        background-color: rgb(236, 236, 236);
        outline: none;
        border: none;
        font-size: 20px;
        width: 100%;
        height: 100vh;
    }
    .nav-links {
        display: none;
    }

    .home,
    .services,
    .faqs,
    .contact{
        color: rgb(31, 31, 56);
        text-align: center;
        padding: 32px;
        width: 100%;
        transition: all 0.5s ease;
    }

    .nav-menu {
        position: absolute;
        display: block;
        font-size: 20px;
        outline: none;
        border: none;
        color: rgb(31, 31, 56);
        padding-left: 5px;
    }

    .navbar {
        width: 100%;
        height: 40px;
        background-color: rgb(236, 236, 236);
        border-bottom: 2px solid lightgray;
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }

    .nav-pic {
        display: block;
        height: 25px;
        margin-left: 96%;
    }
}

@media screen  and (max-width:450px) {
    .logo {
        font-size: 10px;
